@media screen and ( max-width: 1240px ) {
    .header {
        &__title {
            font-size: 180px;
            line-height: 220px;
            padding-top: 120px; }
        &__title span {
            font-size: 42px;
            line-height: 45px;
            right: 164px; } }
    .hotel {
        &__inner {
            display: block; }
        &__images {
            width: 100%;
            margin: 0; }
        &__content {
            max-width: 1240px; }
        &__text {
            margin: 30px 0; }
        &__gallery a {
            margin-top: 30px; } }

    .price {
        background-position: top right 10%;
        background-size: 20%;
        &__off,
        &__on {
            flex: 1; } }

    .contact {
        &__images {
            width: 50%; }
        &__form_name,
        &__form_tel {
            width: 80%; }
        &__form-btn {
            font-size: 22px; } }

    .title {
        &__contact-data span {
            left: 224px; }
        &__contact-amount span {
            left: 292px; } } }






@media screen and ( max-width: 960px ) {
    .page-section {
        margin: 100px 0; }
    .container__fairy-tail,
    .container__our-trip {
        position: static; }
    .fairy-tail,
    .our-trip {
        &__body {
            width: 100%;
            padding-left: 0px;
            padding-right: 0px; }
        &__content {
            width: 100%; }
        &__slider {
            width: 100%; }
        &__text {
            padding: 10px 0; } }
    .our-trip__btn {
        margin-bottom: 50px; }

    .title {
        &__contact-data  span {
            left: 167px; }
        &__contact-amount span {
            left: 264px; } } }

@media screen and ( max-width: 840px ) {
