            right: 109px; }
        &__top {
            font-size: 20px;
            line-height: 24px; }
        &__phone::before {
            width: 25px;
            height: 25px; } }
    .title {
        &__fairy-tail {
            margin-bottom: 25px;
            font-size: 76px;
            line-height: 98px; }
        &__fairy-tail span {
            font-size: 20px;
            line-height: 20px;
            left: 147px; }
        &__our-trip {
            margin-bottom: 25px;
            font-size: 76px;
            line-height: 98px; }
        &__our-trip span {
            font-size: 20px;
            line-height: 20px;
            left: 197px; }
        &__hotel {
            margin-bottom: 25px;
            font-size: 76px;
            line-height: 98px; }
        &__hotel span {
            font-size: 20px;
            line-height: 14px;
            left: 92px; }
        &__price {
            margin-bottom: 35px;
            font-size: 76px;
            line-height: 98px; }
        &__price span {
            font-size: 20px;
            line-height: 20px;
            left: 95px; }
