            text-align: center;
            font-size: 74px; }
        &__balloon {
            margin-top: 0; } } }



@media screen and ( max-width: 600px ) {
    .header {
        height: 100vh;
        &__title {
            font-size: 80px;
            line-height: 120px; }
        &__title span {
