            line-height: 31px;
            left: 117px; }
        &__our-trip {
            margin-bottom: 25px;
            font-size: 60px;
            line-height: 98px; }
        &__our-trip span {
            font-size: 20px;
            line-height: 31px;
            left: 155px; }
        &__hotel {
            margin-bottom: 25px;
            font-size: 60px;
            line-height: 98px; }
        &__hotel span {
            font-size: 20px;
            line-height: 31px;
            left: 73px; }
