html {
    box-sizing: border-box; }

*,*::before, *::after {
    box-sizing: inherit; }

a {
    text-decoration: none;
    color: inherit; }

ul,
li {
    list-style: none;
    margin: 0;
    padding: 0; }

h1,
h2 {}
h3,
h4,
h5,
h6 {
    margin: 0;
    font-size: inherit; }



body {
    min-width: 320px;
    overflow-x: hidden;
    position: relative;
    color: #000;
    @extend %roboto-light;
    font-size: 18px;
    line-height: 21px; }
* {
  outline: 0 !important; }
